/*#region INPUTS**/
input {
	background-color: #313131;
	display: inline-block;
	color: #FFC0CB;
	font-family: "American Typewriter", serif;
	font-size: larger;
	text-align: center;
	text-transform: uppercase;
	outline: none;
	box-sizing: border-box;
	width: 100%;
	height: 100%;
}
.word.dm {
	border-color: #000000;
	background-color: #1e1e1e
}
input::selection { 
	background-color: transparent
}
/*#endregion*/

/*#region KEYS*/
.key{
	border-radius: 2px;
}
.key.r0 {
	width: 10%;
	color: #FFC0CB;
	background-color: #313131;
}
.key.r1 {
	width: 11.11%;
}
.key.r2 {
	width: 10%;
}
.key.delete {
	width: 15%;
	background-image: url(/better-wordle/static/media/backspace-icon.2d22e2c0.png);
	background-repeat: no-repeat;
	background-position: center;
	color: transparent;
	float: right;
}
.key.enter {
	float: left;
	width: 15%;
}
/*#endregion*/

/*#region KEYBOARD BOX*/
.key-row {
	box-sizing: border-box;
	height: 33.33%;
	margin: 0 auto;
	border-color: inherit;
	float: inline-start
}
.key-box {
	height: 100%;
	width: 100%;
	max-width: 520px;
	border-color: #D8BFD8
}
input.dm {
	border-color: #1f1f1f
}
/*#endregion*/

/*#region BUTTONS*/
input.headerButton {
	border-radius: unset;
	margin: unset;
	background-color: unset;
	display: inline-block;
	color: #000000;
	text-transform: unset;
	border-radius: 2px;
	border-width: 2px;
	box-sizing: border-box;
	font-size: 130%;
	font-family: "Times New Roman", serif;
	width: 33.33%;
    min-height: 38px;
	min-width: 132px;
	display: inline-block;
	border-color: #000000;
	background-color: rgb(225, 225, 225)
}
input.headerButton:hover {
	background-color: rgb(203, 168, 202);
	-webkit-transform: scale3d(0.98, 0.96, 1);
	        transform: scale3d(0.98, 0.96, 1)
}
input.headerButton.dm {
	background-color: #313131;
	color: #ffcfcf;
	border-color: #000000
}
input.headerButton:hover.dm {
	background-color: #761919;
	color: #ff6982;
	-webkit-transform: scale3d(0.96, 0.96, 1);
	        transform: scale3d(0.96, 0.96, 1)
}
/*#endregion*/

