/*#region GENERAL COMPONENTS*/
div{
	box-sizing: border-box;
	margin: 0 auto;
	padding-top: unset;
}
body {
	margin: 0 auto;
	padding-top: 0.5%;
	align-content: center;
	text-align: center;
	background-color: #D8BFD8
}
p {
	border: 2px solid black;
	box-sizing: border-box;
	margin: 0;
	font-family: "American Typewriter", serif;
	font-size: 200%;
	display: inline-block
}
.big-box {
	-webkit-user-select: none; /* Safari */ /* Old version of Firefox */ /* Internet Explorer or Edge */
    user-select: none; /* All modern browsers */
}
#headerBox {
	height: -webkit-fit-content;
	height: -moz-fit-content;
	height: fit-content;
	box-sizing: border-box;
	min-width: 500px;
	width: 50%
}
.dm#headerBox {
	background-color: #712b06
}
.container {
	border: 3px solid rgb(255, 148, 71);
	min-width: -moz-fit-content;
	min-width: -webkit-fit-content;
	min-width: fit-content;
}
.container.dm {
	border: 3px solid #712b06
}
/*#endregion*/

/*#region WORD CONTAINERS*/
.row {
	box-sizing: border-box;
	display: inline-block;
	min-width: 200px;
	max-width: -webkit-fit-content;
	max-width: -moz-fit-content;
	max-width: fit-content;
	width: 100%
}
.wordContainer {
	height: 100%;
	min-width: 250px;
	box-sizing: border-box;
	width: 50%;
	min-width: 241.5px;
	height: -webkit-fit-content;
	height: -moz-fit-content;
	height: fit-content;
}
.wordInput{
    text-align: left;
    text-indent: 26px;
    letter-spacing: 56px;
    caret-color: transparent;
    width: 345px;
    height: 49px;
	-webkit-text-fill-color: pink;
}
#wordInputBox{
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    width:350px;
    -webkit-transform: translateY(-2px);
            transform: translateY(-2px)
}
#keyContainer{
	min-width: 350px;
	width: 40%;
	max-width: 520px;
	height: 130px;
    -webkit-transform: translateY(-5px);
            transform: translateY(-5px)
}
/*#endregion*/

/*#region TITLE BOX*/
.title-box {
	font-family: "Times New Roman", serif;
	font-size: 230%;
	display: block;
	width: 100%;
}
#plain {
	background-color: rgb(225, 225, 225)
}
#error {
	background-color: rgb(168, 14, 14)
}
.dm#plain {
	background-color: #262626;
	color: #FFC0CB
}
.dm#error {
	background-color: #570909;
	color: #000000
}
/*#endregion*/

/*#region LETTER BOXES*/
.letter {
	width: 20%;
	height: 100%;
	min-height: 36.8px;
	text-align: center
}
#charColor-2 {
	background-color: rgb(225, 225, 225);
	color: transparent
}
#charColor-1 {
	background-color: rgb(225, 225, 225)
}
#charColor1 {
	background-color: rgb(99, 190, 93)
}
#charColor0 {
	background-color: rgb(255, 199, 30)
}
.dm#charColor-2 {
	background-color: #262626;
	color: transparent
}
.dm#charColor-1 {
	background-color: #262626;
	color: #FFC0CB
}
.dm#charColor1 {
	background-color: #004500;
	color: #FFFFFF
}
.dm#charColor0 {
	background-color: #FFA500;
	color: #892d2d
}
/*#endregion*/

/*#region ANSWER BOXES*/
#ans-box {
	width: 100%;
	font-size: 70%;
	background-color: #262626;
	border-color: #000000;
	font-stretch: condensed;
	height: 40.5px;
	text-indent: 0px;
}
/*#endregion*/

